<template>
  <div id="specialSection" >
    <div class="row ">
      <div class="col-md-12 pdSpecialAbout">
        <p class="text-center text-about title-about">        -  ,     .</p>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12 text-center">
        <p v-if="visible" class="title-about">       ..
             .415  </p>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12 text-center">
        <button class="btn-about"  @click="visible=!visible">{{buttonText}}</button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        visible:false,
      }
    },
    computed: {
      buttonText: function () {
        return this.visible ? ' ' : ' '
      }
    }
  }
</script>




